.more {
  background: #F7F7F7;
  padding: 150px 0;
}

.more__inner {
  display: flex;
  align-items: center;
  margin-left: 7%;
}

.more__picture {
  width: 45.5%;
  margin-right: 13.5%;
  flex-shrink: 0;
  height: 400px;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.more__text {
  width: 66%;
  padding-right: 10%;
}